<template>
  <div>
    <b-breadcrumb :items="menus"/>
    <b-container fluid>
      <div class="addCon">
        <b-button variant="warning" @click="addKit">+ 试剂盒</b-button>
        <b-modal ref="addKitModal" hide-footer no-close-on-backdrop title="试剂盒 -- 添加">
          <b-form class="smForm" style="text-align:left" @submit.prevent="onSubmit($event)">
            <b-form-group id="add_kit_nameGroup" label="试剂盒名称:" label-size="sm" label-for="add_kit_name">
              <b-form-input autocomplete="off" size="sm" type="text" id="add_kit_name" v-model.trim="addForm.kit_name" required>
              </b-form-input>
            </b-form-group>
            <b-form-group id="add_kit_modelGroup" label="试剂盒规格:" label-size="sm" label-for="add_kit_model">
              <b-form-input autocomplete="off" size="sm" type="text" id="add_kit_model" v-model.trim="addForm.kit_model" required>
              </b-form-input>
            </b-form-group>
            <b-form-group id="add_kit_unitGroup" label="试剂盒规格单位:" label-size="sm" label-for="add_kit_unit">
              <b-form-input autocomplete="off" size="sm" type="text" id="add_kit_unit" v-model.trim="addForm.kit_unit" required>
              </b-form-input>
            </b-form-group>
            <b-form-group id="add_kit_providerGroup" label="试剂盒供应商:" label-size="sm" label-for="add_kit_provider">
              <b-form-input autocomplete="off" size="sm" type="text" id="add_kit_provider" v-model.trim="addForm.kit_provider" required>
              </b-form-input>
            </b-form-group>
            <b-form-group id="add_kit_codeGroup" label="试剂盒货号:" label-size="sm" label-for="add_kit_code">
              <b-form-input autocomplete="off" size="sm" type="text" id="add_kit_code" v-model.trim="addForm.kit_code" required>
              </b-form-input>
            </b-form-group>
            <b-form-group id="add_kit_descGroup" label="试剂盒批次:" label-size="sm" label-for="add_kit_desc">
              <ul class="versionUL">
                <li v-for="(item,index) in addForm.kit_desc_arr" :key="index">
                  {{item}}
                  <img src="/images/errortips.png" alt="" title="点击删除" @click="deletekitDescItem(addForm,index)">
                </li>
              </ul>
              <b-input-group size="sm">
                <b-form-input type="text" v-model.trim="addForm.kit_desc_input" placeholder="请输入要添加的批次"></b-form-input>
                <b-input-group-append>
                  <b-btn variant="outline-theme" @click="saveKitVesion(addForm)">添加批次</b-btn>
                </b-input-group-append>
              </b-input-group>
            </b-form-group>
            <b-button type="submit" style="width:100%" variant="info">添加</b-button>
          </b-form>
        </b-modal>
      </div>
      <b-table bordered responsive hover :items="kitLists" :fields="fields" head-variant='light' show-empty :empty-text="tableEmptyText">
        <template slot="kit_desc" slot-scope="row">
          <b-link @click="getDesc(row.item)">{{row.item._showDetails?'收起':'展开'}}</b-link>
        </template>
        <!-- 详情模板 -->
        <template slot="row-details" slot-scope="row">
          <b-card>
            <div v-if="row.item.kit_desc==='[]'||!row.item.kit_desc" style="color:#ccc">暂无批次</div>
            <ul class="versionUL" v-if="row.item.kit_desc!=='[]'&&row.item.kit_desc">
              <li v-for="(item,index) in JSON.parse(row.item.kit_desc)" :key="index">{{item}}</li>
            </ul>
          </b-card>
        </template>
        <template slot="operate" slot-scope="row">
          <b-button size="sm" class="mr-2" variant="outline-theme" @click="edit(row)">
           编辑
          </b-button>
          <b-button size="sm" class="mr-2" variant="danger" @click="openDeleteModal(row)">
           删除
          </b-button>
          <b-modal :ref="'deleteKitModal'+row.index" title="试剂盒 -- 删除" ok-variant="info" cancel-variant="link" cancel-title="取消" ok-title="确定" @ok="deletekit(row)">
            <p class="my-4" style="text-align:left">是否确定要删除 试剂盒名称：<b>{{row.item.kit_name}}</b> ？</p>
          </b-modal>
          <b-modal :ref="'editKitModal'+row.index" hide-footer no-close-on-backdrop title="试剂盒 -- 编辑">
            <b-form class="smForm" style="text-align:left" @submit.prevent="onSubmit($event,row)">
              <b-form-group :id="'edit_kit_nameGroup'+row.index" label="试剂盒名称:" label-size="sm" :label-for="'edit_kit_name'+row.index">
                <b-form-input autocomplete="off" size="sm" type="text" :id="'edit_kit_name'+row.index" v-model.trim="currentForm.kit_name" required disabled>
                </b-form-input>
              </b-form-group>
              <b-form-group :id="'edit_kit_modelGroup'+row.index" label="试剂盒规格:" label-size="sm" :label-for="'edit_kit_model'+row.index">
                <b-form-input autocomplete="off" size="sm" type="text" :id="'edit_kit_model'+row.index" v-model.trim="currentForm.kit_model" required>
                </b-form-input>
              </b-form-group>
              <b-form-group :id="'edit_kit_unitGroup'+row.index" label="试剂盒规格单位:" label-size="sm" :label-for="'edit_kit_unit'+row.index">
                <b-form-input autocomplete="off" size="sm" type="text" :id="'edit_kit_unit'+row.index" v-model.trim="currentForm.kit_unit" required>
                </b-form-input>
              </b-form-group>
              <b-form-group :id="'edit_kit_providerGroup'+row.index" label="试剂盒供应商:" label-size="sm" :label-for="'edit_kit_provider'+row.index">
                <b-form-input autocomplete="off" size="sm" type="text" :id="'edit_kit_provider'+row.index" v-model.trim="currentForm.kit_provider" required>
                </b-form-input>
              </b-form-group>
              <b-form-group :id="'edit_kit_codeGroup'+row.index" label="试剂盒货号:" label-size="sm" :label-for="'edit_kit_code'+row.index">
              <b-form-input autocomplete="off" size="sm" type="text" :id="'edit_kit_code'+row.index" v-model.trim="currentForm.kit_code" required>
              </b-form-input>
            </b-form-group>
            <b-form-group :id="'edit_kit_descGroup'+row.index" label="试剂盒批次:" label-size="sm" :label-for="'edit_kit_desc'+row.index">
              <ul class="versionUL">
                <li v-for="(item,index) in currentForm.kit_desc_arr" :key="index">
                  {{item}}
                  <img src="/images/errortips.png" alt="" title="点击删除" @click="deletekitDescItem(currentForm,index)">
                </li>
              </ul>
              <b-input-group size="sm">
                <b-form-input type="text" v-model.trim="currentForm.kit_desc_input" placeholder="请输入要添加的批次"></b-form-input>
                <b-input-group-append>
                  <b-btn variant="outline-theme" @click="saveKitVesion(currentForm)">添加批次</b-btn>
                </b-input-group-append>
              </b-input-group>
            </b-form-group>
            <b-button type="submit" style="width:100%" variant="info">提交</b-button>
            </b-form>
          </b-modal>
        </template>
      </b-table>
      <div class="paginationCon">
        <b-pagination size="md" :number-of-pages="10" :total-rows="total" v-model="currentPage" @input="changePage" :per-page="10"></b-pagination>
      </div>
    </b-container>
  </div>
</template>
<script>
  export default{
    mounted(){
      
      this.getTableData()
      
    },
    data () {
      return {
        menus: [{
          text: '试剂盒管理',
          href: '#'
        }],
        addForm:{
          kit_name:'',
          kit_model:'',
          kit_unit:'',
          kit_provider:'',
          kit_code:'',
          kit_desc_arr:[],
          kit_desc_input:'',
        },
        currentForm:{
          kit_name:'',
          kit_model:'',
          kit_unit:'',
          kit_provider:'',
          kit_code:'',
          kit_desc_arr:[],
          kit_desc_input:'',
        },
        fields: {
          kit_name:{
            label:'试剂盒名称'
          },
          kit_model:{
            label:'试剂盒规格'
          },
          kit_unit:{
            label:'试剂盒规格单位'
          },
          kit_provider:{
            label:'试剂盒供应商'
          },
          kit_code:{
            label:'试剂盒货号'
          },
          kit_desc:{
            label:'试剂盒批次'
          },
          operate:{
            label:'操作',
            thStyle:{
              width:'150px'
            }
          }
        },
        tableEmptyText:'正在请求数据...',
        kitLists: [],
        currentPage:1,
        total:0
      }
    },
    methods:{
      getTableData(){
        this.$Loading.start()
        this.tableEmptyText = "正在请求数据..."
        this.$http.post("/api/kit/list",qs.stringify({page:this.currentPage})).then(response=>{
          // console.log(response.data)
          this.kitLists=response.data.data
          this.kitLists.forEach(item=>{
            this.$set(item,'_showDetails',false)
          })
          Number(response.data.total)===0?this.tableEmptyText = "[空]":null
          this.total = Number(response.data.total)
          this.$Loading.finish()
        })
      },
      getDesc(item){
        if(!item._showDetails){
          item._showDetails=true
        }else{
          item._showDetails=false
        }
      },
      saveKitVesion(form){
        // 空值不能提交
        if(form.kit_desc_input===''){
          this.$Message.error("请先输入试剂盒批次，再提交！")
          return false
        }
        // 控制该批次是否已存在
        if(form.kit_desc_arr.filter(item=>{
          return item===form.kit_desc_input
        }).length===0){
          form.kit_desc_arr.push(form.kit_desc_input)
          form.kit_desc_input=''
        }else{
          this.$Message.error("该批次已存在！请勿重复添加")
        }
      },
      deletekitDescItem(form,index){
        // console.log(index);
        form.kit_desc_arr.splice(index,1)
      },
      addKit(){
        this.$refs.addKitModal.show()
      },
      edit(row){
        if(this.currentForm.kitid!==row.item.kitid){
          for(var k in row.item){
            if(k==="kit_desc"){
              this.currentForm.kit_desc_arr=JSON.parse(row.item[k])
            }else{
              this.currentForm[k]=row.item[k]
            }
          }
        }
        this.$refs['editKitModal'+row.index].show()
      },
      openDeleteModal(row){
        this.$refs['deleteKitModal'+row.index].show()
      },
      deletekit(row){
        this.$http.post("/api/kit/del",qs.stringify({kitid:row.item.kitid})).then(response=>{
          this.getTableData()
          this.$Loading.finish()
        })
      },
      onSubmit(evt,row){
        this.$Loading.start()
        evt.preventDefault();
        if(row){
          if(this.currentForm.kit_desc_arr.length===0){
            this.$Message.warning("请添加试剂盒批次!");
            this.$Loading.error()
            return false;
          }
          this.currentForm.kit_desc=JSON.stringify(this.currentForm.kit_desc_arr)
          this.$http.post("/api/kit/edit",qs.stringify(this.currentForm)).then(response=>{
            this.getTableData()
            this.currentForm={
              kit_name:'',
              kit_model:'',
              kit_unit:'',
              kit_provider:'',
              kit_code:'',
              kit_desc_arr:[],
              kit_desc_input:'',
            }
            this.$refs['editKitModal'+row.index].hide()
            this.$Loading.finish()
          })
        }else{
          // 添加
          // console.log(this.addForm)
          if(this.addForm.kit_desc_arr.length===0){
            this.$Message.warning("请添加试剂盒批次!");
            this.$Loading.error()
            return false;
          }
          this.addForm.kit_desc=JSON.stringify(this.addForm.kit_desc_arr)
          this.$http.post("/api/kit/add",qs.stringify(this.addForm)).then(response=>{
            this.getTableData()
            this.addForm={
              kit_name:'',
              kit_model:'',
              kit_unit:'',
              kit_provider:'',
              kit_code:'',
              kit_desc_arr:[],
              kit_desc_input:'',
            }
            this.$refs.addKitModal.hide()
            this.$Loading.finish()
          })
        }
      },
      changePage(){
        this.getTableData()
      }
    }

  }
</script>
<style lang='less' scoped>
@import '../../../../less/init.less';
.versionUL{
  display:flex;
  flex-wrap:wrap;
  margin-bottom:5px;
  li{
    padding: 5px 10px;
    margin: 5px;
    background-color:#eee;
    border-radius:5px;
    position:relative;
    img{
      position:absolute;
      top:-6px;
      right:-6px;
    }
  }
}
.btn-sm{
  padding: .08rem .5rem !important;
}
.addCon{
  text-align: right;
  padding-right: 20px;
  margin-bottom:10px;
}
.paginationCon{
  overflow: hidden;
  .pagination{
    float: right;
  }
}

</style>


